.banner {
  position: relative;
  min-height: 100vh;
  height: 100%;

  &__image {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  &__container {
    min-height: 100vh;
  }
  &__body {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    padding: 140px 0 18px;
  }
  &__form {
    flex: 1 1 auto;
    max-width: 380px;
    margin-bottom: 50px;
  }
  &__text {
    max-width: 297px;
    align-self: end;
    &.type-body {
      text-align: right;
    }
  }
}

@media (max-width: 380px) {
  .card {
    padding: 20px 10px 20px;
    border: 1px solid $dark-50;
    transform: scale(90%);
  }
  .banner__body {
    margin: 0 -30px;
  }
  .banner__text {
    align-self: center;
    &.type-body {
      text-align: center;
    }
  }
}
